<template>
  <div class="component item">
    <button
      class="btn minus"
      v-if="type === 'right'"
      @click="$store.dispatch('delItem', { item })"
    >&lt;=</button>
    <pre>{{ item }}</pre>
    <button
      class="btn plus"
      v-if="type === 'left'"
      @click="$store.dispatch('addItem', { item })"
    >=&gt;</button>
  </div>
</template>

<script lang="ts">
import { Options, Vue } from 'vue-class-component';

@Options({
  props: {
    item: Object,
    type: String,
  },
})
export default class HelloWorld extends Vue {
  item!: Item

  type!: ItemType
}
</script>

